.free-trial {
    margin-bottom: 79px;

    .container {
        width: 1200/1440 * 100%;
        padding: 63px 0 26px;
        text-align: center;
        background-color: $color9;

        h2 {
            font-size: 4.6rem;
            line-height: 51/46 * 100%;
            color: $color3;
            margin-bottom: 65px;
        }

        .free-trial__services {
            width: 666.66/1200 * 100%;
            display: flex;
            justify-content: space-between;
            margin:  0 auto 70px;

            .free-trial__services__service {
                width: 200/666.66 * 100%;
    
                div {
                    width: 64px;
                    height: 64px;
                    margin: 0 auto 22px;
                    background-repeat: no-repeat;
                }
    
                &:first-child div {
                    background-image: url("./assets/cart.png");
                }
    
                &:nth-child(2) div {
                    background-image: url("./assets/charger.png");
                }
    
                &:last-child div {
                    background-image: url("./assets/wallet.png");
                }
            }
        }

        button {
            width: 146/1200 * 100%;
            height: 50px;
            background-color: $color7;
            color: white;
            font-size: 1.2rem;
            line-height: 50/12 * 100%;
            letter-spacing: 2/1440 * 100vw;
            margin-bottom: 26px;
            cursor: pointer;
        }

        .free-trial__terms {
            font-size: 1.2rem;
            line-height: 14.06/12 * 100%;
            color: $color2;
        }
    }
}