.principles {
    margin-bottom: 288/1440 * 100vw;

    .container {
        width: 1200/1440 * 100%;

        .description {
            width: 882/1200 * 100%;
            font-size: 4.6rem;
            line-height: 52/46 * 100%;
            margin: 0 auto 135/1440 * 100vw;

            p:first-child {
                color: $color2;
                margin-bottom: 25px;
            }

            p:last-child {
                color: $color3;
            }
        }

        @media (max-width: 1310px) {
            .description {
                width: 84%;
            }
        }

        @media (max-width: 1146px) {
            .description {
                width: 94%;
            }
        }

        @media (max-width: 1024px) {
            .description {
                font-size: 3.5rem;
            }
        }

        @media (max-width: 683px) {
            .description {
                font-size: 2.6rem;
            }
        }

        @media (max-width: 507px) {
            .description {
                font-size: 2rem;
            }
        }

        @media (max-width: 390px) {
            .description {
                font-size: 1.5rem;
            }
        }

        .principles__principle {
            display: flex;
            column-gap: 90/1200 * 100%;
            align-items: center;

            .principles__principle__image {
                width: 671/1200 * 100%;
                aspect-ratio: 1;
                background-size: contain;
            }

            &._image-left .principles__principle__image {
                margin-left: -110%;
            }

            &._image-right .principles__principle__image {
                margin-right: -110%;
            }

            @media (max-width: 907px) {
                .principles__principle__image {
                    width: 51%;
                }
            }

            @media (max-width: 796px) {
                .principles__principle__image {
                    width: 46%;
                }
            }

            @media (max-width: 636px) {
                .principles__principle__image {
                    width: 46%;
                }
            }

            &._image-left .principles__principle__image {
                background-image: url("./assets/first-principle-image.png");
            }

            &._image-right .principles__principle__image {
                background-image: url("./assets/second-principle-image.png");
            }

            &:last-child .principles__principle__image {
                background-image: url("./assets/third-principle-image.png");
            }

            .principles__principle__description {
                width: 306/1200 * 100%;

                h3 {
                    font-size: 3.2rem;
                    line-height: 36/32 * 100%;
                    color: $color5;
                    margin-bottom: 27px;
                }

                @media (max-width: 683px) {
                    h3 {
                        font-size: 2.4rem;
                    }
                }

                @media (max-width: 507px) {
                    h3 {
                        font-size: 1.8rem;
                    }
                }

                @media (max-width: 390px) {
                    h3 {
                        font-size: 1.2rem;
                    }
                }

                @media (max-width: 358px) {
                    h3 {
                        margin-bottom: 3px;
                    }
                }

                p {
                    font-size: 1.6rem;
                    line-height: 24/16 * 100%;
                    color: $color2;
                }

                @media (max-width: 636px) {
                    p {
                        font-size: 1.2rem;
                    }
                }

                @media (max-width: 477px) {
                    p {
                        font-size: 1rem;
                    }
                }
            }

            @media (max-width: 1280px) {
                .principles__principle__description {
                    width: 31%;
                }
            }

            @media (max-width: 1053px) {
                .principles__principle__description {
                    width: 36%;
                }
            }

            @media (max-width: 907px) {
                .principles__principle__description {
                    width: 41%;
                }
            }

            @media (max-width: 796px) {
                .principles__principle__description {
                    width: 45%;
                }
            }

            &._image-right {
                flex-direction: row-reverse;
            }

            &:not(:last-child) {
                margin-bottom: 64px;
            }
        }
    }

    @media (max-width: 779px) {
        .container {
            width: 95%;
        }
    }
}