.types-of-rooms-section {
    margin-bottom: 247/1440 * 100vw;

    .container {
        padding: 0 120/1440 * 100%;

        .types-of-rooms-section__description {
            width: 100%;
            display: flex;
            column-gap: 9%;
            padding: 0 102/1440 * 100% 0 66/1440 * 100%;
            margin-bottom: 208/1440 * 100vw;
            margin-left: -105%;

            p {
                &:first-child {
                    width: 33%;
                    font-size: 4.8rem;
                    line-height: 58/48 * 100%;
                    color: $color5;
                }

                @media (max-width: 1410px) {
                    &:first-child {
                        width: 38%;
                    }
                }

                @media (max-width: 1306px) {
                    &:first-child {
                        width: 43%;
                    }
                }

                @media (max-width: 1166px) {
                    &:first-child {
                        width: 53%;
                    }
                }

                @media (max-width: 1024px) {
                    &:first-child {
                        width: 63%;
                    }
                }

                @media (max-width: 861px) {
                    &:first-child {
                        width: 73%;
                    }
                }

                @media (max-width: 575px) {
                    &:first-child {
                        width: 88%;
                    }
                }

                @media (max-width: 477px) {
                    &:first-child {
                        width: 95%;
                    }
                }

                @media (max-width: 442px) {
                    &:first-child {
                        font-size: 3.6rem;
                    }
                }

                @media (max-width: 331px) {
                    &:first-child {
                        font-size: 2.7rem;
                    }
                }

                &:last-child {
                    width: 54%;
                    font-size: 2.4rem;
                    line-height: 40/24 * 100%;
                    color: $color2;
                }

                @media (max-width: 1324px) {
                    &:last-child {
                        width: 65%
                    }
                }

                @media (max-width: 1166px) {
                    &:last-child {
                        width: 74%
                    }
                }

                @media (max-width: 863px) {
                    &:last-child {
                        width: 84%
                    }
                }

                @media (max-width: 760px) {
                    &:last-child {
                        width: 94%
                    }
                }

                @media (max-width: 679px) {
                    &:last-child {
                        font-size: 1.8rem;
                    }
                }

                @media (max-width: 509px) {
                    &:last-child {
                        font-size: 1.4rem;
                    }
                }

                @media (max-width: 396px) {
                    &:last-child {
                        font-size: 1rem;
                    }
                }
            }
        }

        @media (max-width: 1291px) {
            .types-of-rooms-section__description {
                padding: 0 2% 0 66/1440 * 100%;
            }
        }

        @media (max-width: 1166px) {
            .types-of-rooms-section__description {
                flex-direction: column;
                align-items: center;
                row-gap: 15px;
            }
        }

        .types-of-rooms-section__rooms {
            display: flex;
            column-gap: 24/1200 * 100%;
            row-gap: 32px;
            flex-wrap: wrap;

            .types-of-rooms-section__rooms__room {
                width: 588/1200 * 100%;
                display: flex;
                flex-direction: column;
                row-gap: 9px;
                column-gap: 0;
    
                div {
                    width: 100%;
                    aspect-ratio: 588/499;
                    background-size: contain;
                }
                
                &:first-child div {
                    background: url("./assets/types-of-rooms__phone-booth.png") center no-repeat;
                }

                &:nth-child(2) div {
                    background: url("./assets/types-of-rooms__focus-room.png") center no-repeat;
                }

                &:nth-child(3) div {
                    background: url("./assets/types-of-rooms__meeting-room.png") center no-repeat;
                } 

                &:last-child div {
                    background: url("./assets/types-of-rooms__open-meeting-room.png") center no-repeat;
                }

                p {
                    font-size: 2.4rem;
                    line-height: 28/24 * 100%;
                    color: $color5;
                }
            }

            @media (max-width: 765px) {
                .types-of-rooms-section__rooms__room {
                    width: 69%;
                }
            }

            @media (max-width: 378px) {
                .types-of-rooms-section__rooms__room {
                    width: 84%;
                }
            }
        }

        @media (max-width: 765px) {
            .types-of-rooms-section__rooms {
                flex-direction: column;
                align-items: center;
            }
        }
    }
}