.above__header {
    background-color: $color1;
    font-size: 1.2rem;
    line-height: 33/12 * 100%;
    letter-spacing: 1px;
    color: $color2;

    .container {
        max-width: 1440px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        padding: 10px 25/1440 * 100% 3.5px;
        justify-content: flex-end;
        column-gap: 25/1440 * 100%;
    }
}