@keyframes image-smalling {
    from {
        width: 120%;
    }
    to {
        width: 100%;
    }
}

.welcome-section {
    margin-bottom: 80px;
    
    .container {
        max-width: 120vw;
        width: 100vw;
        aspect-ratio: 1440/810;
        overflow: hidden;
        
        img {
            width: 120%;
            aspect-ratio: 1440/810;
            animation: image-smalling 1500ms ease forwards;
        }
    }
}