.advantages {
    margin-bottom: 240/1440 * 100vw;

    .container {
        width: 1090/1440 * 100%;

        h2 {
            font-size: 4.8rem;
            line-height: 56.25/48 * 100%;
            color: $color5;
            margin-bottom: 24px;
            text-align: center;
        }

        @media (max-width: 947px) {
            h2 {
                font-size: 3.6rem;
            }
        }

        @media (max-width: 710px) {
            h2 {
                font-size: 2rem;
            }
        }

        a {
            font-size: 1.4rem;
            line-height: 12/14 * 100%;
            letter-spacing: 0.75px;
            margin-bottom: 71px;
            display: block;
            cursor: pointer;
        }

        .advantages__main {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            .advantages__main__card {
                width: 323.33/1090 * 100%;

                div {
                    width: 162px;
                    height: 121px;
                    margin: 0 auto 26px;
                }

                &:first-child div {
                    background-image: url("./assets/cost-effective.png");
                }

                &:nth-child(2) div {
                    background-image: url("./assets/time-saving.png");
                }

                &:last-child div {
                    background-image: url("./assets/flexible.png");
                }

                h3 {
                    font-size: 2.4rem;
                    line-height: 100%;
                    color: $color5;
                    margin-bottom: 18px;
                }

                p {
                    font-size: 1.6rem;
                    line-height: 24/16 * 100%;
                    color: $color2;
                }

                @media (max-width: 353px) {
                    p {
                        font-size: 1.2rem;
                    }
                }

            } 

            @media (max-width: 1131px) {
                .advantages__main__card {
                    width: 35%;
                }
            }

            @media (max-width: 958px) {
                .advantages__main__card {
                    width: 40%;
                }
            }

            @media (max-width: 838px) {
                .advantages__main__card {
                    width: 50%;
                }
            }

            @media (max-width: 671px) {
                .advantages__main__card {
                    width: 60%;
                }
            }

            @media (max-width: 559px) {
                .advantages__main__card {
                    width: 75%;
                }
            }

            @media (max-width: 447px) {
                .advantages__main__card {
                    width: 95%;
                }
            }
        }

        @media (max-width: 1131px) {
            .advantages__main {
                justify-content: center;
                column-gap: 5%;
                row-gap: 50px;
            }
        }
    }

    @media (max-width: 1419px) {
        .container {
            width: 86%;
        }
    }

    @media (max-width: 1249px) {
        .container {
            width: 95%;
        }
    }
}