.footer {
    background-color: $color10;
    padding: 97px 0 51px;
    color: white;
    
    .container {
        width: 1200/1440 * 100%;
        padding: 0 35/1440 * 100% 51px 55/1440 * 100%;
        display: flex;
        justify-content: space-between;

        .footer__logo {
            width: 87px;
            height: 94px;
            background-image: url("./assets/footer__logo.png");
        }

        @media (max-width: 901px) {
            .footer__logo {
                align-self: flex-start;
            }

        } 

        .footer__info {
            width: 340/1200 * 100%;
            display: flex;
            flex-wrap: wrap;
            row-gap: 20px;
            font-size: 1.4rem;
            line-height: 16.41/14 * 100%;
            letter-spacing: 0.5/1440 * 100vw;

            .footer__info__section {
                width: 50%;

                p:first-child {
                    margin-bottom: 19px;
                    cursor: unset;
                }
    
                p {
                    margin-bottom: 4px;
                    cursor: pointer;
                }
            }
        }

        @media (max-width: 1057px) {
            .footer__info {
                width: 33%;
            }
        }

        @media (max-width: 901px) {
            .footer__info {
                width: 48%;
            }
        }

        @media (max-width: 611px) {
            .footer__info {
                width: 58%;
            }
        }

        @media (max-width: 503px) {
            .footer__info {
                width: 73%;
            }
        }

        @media (max-width: 397px) {
            .footer__info {
                width: 93%;
            }
        }

        .footer__following {
            width: 129/1200 * 100%;

            p {
                font-size: 1.4rem;
                line-height: 21/14 * 100%;
                letter-spacing: 0.5/1440 * 100vw;
                margin-bottom: 17px;
            }

            .footer__folowing__contacts {
                display: flex;
                justify-content: space-between;

                div {
                    width: 20px;
                    height: 20px;
                    cursor: pointer;
                }

                div._facebook {
                    background-image: url("./assets/facebook.png");
                }

                div._instagram {
                    background-image: url("./assets/instagram.png");
                }

                div._linkedin {
                    background-image: url("./assets/linkedin.png");
                }

                div._pinterest {
                    background-image: url("./assets/pinterest.png");
                }
            }
        }

        @media (max-width: 901px) {
            .footer__following {
                width: 129px
            }
        }

        .footer__joining {
            width: 417/1200 * 100%;

            p:first-child {
                font-size: 1.3rem;
                line-height: 21/13 * 100%;
                margin-bottom: 17px;
            }

            p:nth-child(2) {
                font-size: 1.4rem;
                line-height: 16.41/14 * 100%;
                margin-bottom: 32px;
            }

            input[type="text"] {
                width: 100%;
                height: 54px;
                outline: 1px solid white;
                padding: 0 5%;
                border-radius: 3/1440 * 100vw;
                margin-bottom: 16px;
                background-color: $color10;
                color: white;
                font-size: 1.5rem;
            }

            select {
                width: 112/417 * 100%;
                margin: 0 0 56px auto;
                outline: none;
                background-color: $color10;
                display: block;
                color: rgba(255, 255, 255, 0.5);
                appearance: none;
                overflow: hidden;
                background: url("./assets/select-arrow.png") no-repeat right;
                cursor: pointer;

                option {
                    font-size: 1.4rem;
                    line-height: 21/14 * 100%;
                    opacity: 0.5;
                    background-color: $color10;
                    display: block;
                }
            }

            input[type="checkbox"]:checked + label div {
                background-image: url("./assets/check-mark.jpg");
            }

            label {
                display: flex;
                justify-content: space-between;
                margin-bottom: 58px;

                div {
                    width: 28px;
                    height: 26px;
                    border: 1px solid rgba(255, 255, 255, 0.5);
                    cursor: pointer;
                }

                p {
                    width: 369/417 * 100%;
                    font-size: 1.1rem;
                    line-height: 12.89/11 * 100%;
                    letter-spacing: 2.4/1440 * 100vw;
                    color: white;
                    opacity: 0.5;
                }

                @media (max-width: 333px) {
                    p {
                        width: 93%;
                    }
                }
            }

            @media (max-width: 333px) {
                label {
                    flex-direction: column;
                    row-gap: 10px;
                }
            }

            p:last-child {
                font-size: 1.4rem;
                letter-spacing: 2.3/1440 * 100vw;
                cursor: pointer;
            }
        }
        
        @media (max-width: 1349px) {
            .footer__joining {
                width: 40%;
            }
        }

        @media (max-width: 901px) {
            .footer__joining {
                width: 55%;
            }
        }

        @media (max-width: 649px) {
            .footer__joining {
                width: 65%;
            }
        }

        @media (max-width: 649px) {
            .footer__joining {
                width: 75%;
            }
        }

        @media (max-width: 427px) {
            .footer__joining {
                width: 95%;
            }
        }

    }

    @media (max-width: 1227px) {
        .container {
            width: 93%;
        }
    }

    @media (max-width: 1083px) {
        .container {
            width: 95%;
        }
    }

    @media (max-width: 901px) {
        .container {
            flex-direction: column;
            align-items: center;
            row-gap: 50px;
        }
    }
}