.header {
    font-size: 1.2rem;
    line-height: 14.4/12 * 100%;
    letter-spacing: 2px;
    color: $color3;
    border-bottom: 1px solid $color4;
    margin-bottom: 49px;

    .container {
        padding: 25px 31/1440 * 100% 24px 120/1440 * 100%;
        display: flex;
        align-items: center;

        .header__logo {
            width: 50px;
            aspect-ratio: 1;
            background: url("./assets/header__logo.png") center;
            background-size: contain;
            margin-right: 292/1440 * 100%;
        }

        @media (max-width: 584px) {
            .header__logo {
                width: 38px;
            }
        }

        @media (max-width: 499px) {
            .header__logo {
                margin-right: 15%;
            }
        }

        @media (max-width: 384px) {
            .header__logo {
                margin-right: 10%;
            }
        }


        nav {
            width: 462/1440 * 100%;
            display: flex;
            justify-content: space-between;
            margin-right: 402/1440 * 100%;
        }

        a {
            cursor: pointer;
            color: black;
        }

        @media (max-width: 1299px) {
            nav {
                margin-right: 23%;
            }
        }

        @media (max-width: 999px) {
            nav {
                margin-right: 18%;
            }
        }

        @media (max-width: 799px) {
            nav {
                margin-right: 13%;
            }
        }

        @media (max-width: 699px) {
            nav {
                width: max-content;
                flex-direction: column;
                margin-right: 18%;
                row-gap: 3px;
            }
        }

        @media (max-width: 549px) {
            nav {
                margin-right: 8%;
            }
        }

        @media (max-width: 384px) {
            nav {
                margin-right: 3%;
            }
        }

        @media (max-width: 339px) {
            nav {
                margin-right: 8%;
            }
        }

        .header__showrooms {
            width: 133px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            div {
                width: 20px;
                height: 24px;
                background: url("./assets/header__geolocation.png") center no-repeat;
                cursor: pointer;
            }
        }

        @media (max-width: 339px) {
            .header__showrooms {
                width: 100px;
            }
        }

        .header__basket {
            width: 25px;
            height: 25px;
            background: url("./assets/header__basket.png") center no-repeat;
            margin-left: auto;
            cursor: pointer;
        }
    }

    @media (max-width: 449px) {
        .container {
            padding-left: 3%;
        }
    }
}

@media (max-width: 339px) {
    .header {
        font-size: 0.9rem;
    }
}