.carousel {
    margin-bottom: 226px;

    .container {
        width: 1344/1440 * 100vw;
        position: relative;

        .carousel__main {
            overflow-x: visible;
            display: flex;
            margin-bottom: 24px;
            transition: all 1000ms ease;

            &.first-image {
                margin-left: 0;
                transition: 1s;
            }

            &.second-image {
                margin-left: -93vw;
                transition: 1s;
            }

            &.third-image {
                margin-left: -183vw;
                transition: 1s;
            }

            & > img {
                width: 1344/1440 * 100vw;
                flex-shrink: 0;
                flex-grow: 0;
            }
        }

        .carousel__main.first-image + .carousel__flipper div:first-child {
            background-color: $color7;
        }

        .carousel__main.second-image + .carousel__flipper div:nth-child(2) {
            background-color: $color7;
        }

        .carousel__main.third-image + .carousel__flipper div:last-child {
            background-color: $color7;
        }

        .carousel__flipper {
            width: 60px;
            display: flex;
            justify-content: space-between;
            margin: 0 auto;
            
            div {
                display: block;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                background-color: $color8;
                cursor: pointer;
            }
        }

        .caruosel__arrow {
            position: absolute;
            width: 23px;
            height: 42px;
            background-image: url("./assets/arrow.png");
            top: 328/1440 * 100vw;
            cursor: pointer;

            &._left {
                left: 60/1440 * 100vw;
            }
    
            &._right {
                left: 1190/1344 * 100vw;
                transform: rotate(180deg);
            }
        }
        
    }
}