.reviews {
    color: white;
    margin-bottom: 240/1440 * 100vw;

    .container {
        width: 1340/1440 * 100%;
        height: 800/1440 * 100vw;
        padding: 120/1440 * 100vw 0 129/1440 * 100vw 195/1440 * 100vw;
        background: url("./assets/reviews-section-background.png");
        background-size: contain;

        .reviews__review {
            width: 350/1340 * 100%;

            .reviews__review__stars {
                width: 105/1440 * 100vw;
                height: 20/1440 * 100vw;
                background-image: url("./assets/star.png");
                margin-bottom: 14/1440 * 100vw;
                background-size: contain;
            }

            p {
                font-size: 24/1440 * 100vw;
                line-height: 32/24 * 100%;
                margin-bottom: 8/1440 * 100vw;
            }

            .reviews__review__company {
                width: 100/1440 * 100vw;
                background-size: contain;
            }

            &:first-child .reviews__review__company {
                height: 32/1440 * 100vw;
                background-image: url("./assets/reviews__google.png");
            }

            &:nth-child(2) .reviews__review__company {
                height: 42/1440 * 100vw;
                background-image: url("./assets/wayfair.png");
            }

            &:last-child .reviews__review__company {
                height: 22/1440 * 100vw;
                background-image: url("./assets/flexport.png");
            }

            &:not(:last-child) {
                margin-bottom: 32/1440 * 100vw;
            }

            &:last-child {
                margin-bottom: 39/1440 * 100vw;
            }
        }

        a {
            font-size: 16/1440 * 100vw;
            line-height: 12/16 * 100%;
            letter-spacing: 0.75/1440 * 100vw;
            color: white;
            cursor: pointer;
        }
    }
}