.brands {
    .container {
        padding: 0 180/1440 * 100%;

        h2 {
            margin: 0 auto;
            font-size: 4.8rem;
            line-height: 100%;
            color: $color5;
            margin: 0 auto 27px;
            width: 941/1080 * 100%;
            text-align: center;
        }

        @media (max-width: 1374px) {
            h2 {
                width: 95%;
            }
        }

        @media (max-width: 1260px) {
            h2 {
                font-size: 3.6rem;
            }
        }

        @media (max-width: 335px) {
            h2 {
                font-size: 2.7rem;
            }
        }

        .brands__description {
            font-size: 2.4rem;
            line-height: 36/24 * 100%;
            color: $color6;
            margin: 0 auto 125/1440 * 100vw;
            width: max-content;
        }

        .brands__logos {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 49px;
            margin-bottom: 190/1440 * 100vw;

            .brands__logos__logo {
                width: 161px;
                height: 60px;
                text-align: center;
            }
        }

        @media (max-width: 429px) {
            .brands__logos {
                justify-content: center;
            }
        }
    }
}